@font-face {
  font-family: "MyriadPro";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local("MyriadPro Regular"), local("MyriadPro-Regular"), url("../fonts/MyriadPro/MyriadPro-Regular.woff2") format("woff2"), url("../fonts/MyriadPro/MyriadPro-Regular.woff") format("woff"); }

html {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.38;
  font-family: "Rubik", "Arial", sans-serif;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  background-color: #000000; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 0; }

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px; }

.header {
  padding: 38px 0; }
  .header__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end; }
  .header__item {
    margin-right: 24px; }
    .header__item:last-child {
      margin-right: 0; }
  .header__link {
    font-size: 16px;
    font-family: "MyriadPro", "Arial", sans-serif;
    color: white;
    line-height: 1.25;
    text-decoration: none; }

.intro {
  padding-top: 347px; }
  .intro__title {
    font-size: 60px;
    font-family: "Rubik", "Arial", sans-serif;
    font-weight: 700;
    color: white;
    line-height: 1.083;
    margin: 0 0 50px 0; }
  .intro__description {
    font-size: 16px;
    font-family: "Rubik", "Arial", sans-serif;
    color: white;
    line-height: 1.75;
    margin: 0 0 40px 0; }
